@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700;900&display=swap);
body {
	margin: 0;
}
#homepage {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 100vh;
	width: 100vw;
}

#homepage header h1 {
	color: #ffca19;
	font-size: 4em;
}
.marker {
	background-size: cover;
	background-position: center center;
	border: 2px solid white;
	border-radius: 0.7em;
}

.mapboxgl-popup {
	font-size: 1.2em;
	max-width: 200px;
}

.mapboxgl-popup-content {
	padding: 1em;
}

.mapboxgl-popup-close-button {
	font-size: 1.2em;
	padding: 0 0.2em;
}
#map-container { 
	display: flex;
	justify-content: center;
}
.edit_trip_form_container {
	margin: 0 auto;
	padding: 1em;
	text-align: center;
	width: 35vw;
}

.edit_trip_form_container h2 {
	color: #A42E31;
	font-size: 1.7em;
	margin: 0 0 0.5em;
}

.edit_trip_form_container label {
	color: #347792;
}

.edit_trip_form_container section {
	margin-bottom: 1em;
}

.edit_trip_form_container--title input, .edit_trip_form_container--date input, .edit_trip_form_container--places input {
	font-size: 0.85em;
	margin-top: 0.5em;
	padding: 0.5em;
}

.edit_trip_form_container section strong {
	font-size: 1.3em;
}

.edit_trip_form_container--title input {
	width: 30vw;
}

.edit_trip_form_container--date input {
	width: 4em;
}

.edit_trip_form_container--buttons {
	text-align: center;
}

.edit_trip_form_container--buttons button {
	margin-right: 1em;
}

.edit_trip_form_container--buttons button, .edit_trip_form_container--buttons input, .edit_trip_form_container--places button {
	background-color: #347792;
	border: none;
	color: #fff;
	font-size: 0.9em;
	padding: 0.7em;
}

.edit_trip_form_container--buttons button:hover , .edit_trip_form_container--buttons input:hover {
	background-color: #A42E31;
}

.edit_trip_form_container--places button {
	background: none;
	border: none;
}

.edit_trip_form_container--places button:hover {
	color: #ffca19;
	cursor: crosshair;
}

.edit_trip_form_container--places input {
	margin-right: 0.5em;
	width: 14vw;
}
.trip_listing_container {
	background-color: #cf7567;
	box-shadow: 10px 10px #B3CBB9;
	margin-bottom: 1.5em;
	padding: 0 1.7em 1.5em;
	text-align: center;
}

.trip_listing_container h2 {
	color: #fff;
	font-size: 1.7em;
}

.trip_listing_container h3 {
	color: #fbe391;
	font-size: 1.4em;
	margin: 0;
}

.trip_list_container--header {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.trip_listing_container--date {
	color: #DDD8B8;
	font-size: 1.4em;
}

.trip_listing_container--places-container {
	margin: 0 auto;
	width: 30vw;
}

.trip_listing_container--places-container img {
	width: 4em;
	height: 4em;
	margin-right: 1em;
	object-fit: cover;
}

.trip_listing_container--place {
	border: 1px solid #DDD8B8;
	color: #fff;
	display: flex;
	align-items: center;
	margin: 1em 0;
	padding: 1em;
	text-transform:capitalize;
}

.trip_listing_container--buttons button {
	background-color: #EDDDD4;
	border: none;
	color: #772E25;
	font-size: 0.9em;
	padding: 0.7em;
}

.trip_listing_container--buttons button:first-of-type {
	margin-right: 1em;
}

.trip_listing_container--buttons button:hover {
	background-color: #56638A;
	color: #fff;
	cursor: crosshair;
}
.all-trips-container {
	width: 60vw;
}

.all-trips-container h1 {
	color: #56638A;
}
.new_trip_form_container {
	background-color: #B3CBB9;
	margin-top: 2em;
	padding: 1em;
	text-align: center;
	width: 35vw;
}

.new_trip_form_container h2 {
	color: #C44536;
	font-size: 1.7em;
	margin: 0 0 0.5em;
}

.new_trip_form_container label {
	color: #197278;
}

.new_trip_form_container section {
	margin-bottom: 1em;
}

.new_trip_form_container--title input, .new_trip_form_container--date input, .new_trip_form_container--places input {
	font-size: 0.85em;
	margin-top: 0.5em;
	padding: 0.5em;
}

.new_trip_form_container section strong {
	font-size: 1.3em;
}

.new_trip_form_container--title input {
	width: 30vw;
}

.new_trip_form_container--date input:first-of-type {
	width: 2.5em;
}

.new_trip_form_container--date input:last-of-type {
	margin-left: 0.2em;
	width: 3.5em;
}

.new_trip_form_container--buttons {
	text-align: center;
}

.new_trip_form_container--buttons button {
	margin-right: 1em;
}

/* Cancel Button */
.new_trip_form_container--buttons button:last-of-type {
	background-color: #ECA72C;
	margin-top: 1em;
}

.new_trip_form_container--buttons button, .new_trip_form_container--buttons input, .new_trip_form_container--places button {
	background-color: #197278;
	border: none;
	color: #fff;
	font-size: 0.9em;
	padding: 0.7em;
}

.new_trip_form_container--buttons button:hover , .new_trip_form_container--buttons input:hover {
	background-color: #A42E31;
}

.new_trip_form_container--places button {
	background: none;
	border: none;
}

.new_trip_form_container--places button:hover {
	color: #ffca19;
	cursor: crosshair;
}

.new_trip_form_container--places input {
	margin-right: 0.5em;
	width: 14vw;
}

/* TOGGLE BUTTON */

.new_trip_form_button {
	background-color: #B3CBB9;
	color: #C05746;
	font-size: 1.1em;
	font-weight: 700;
	letter-spacing: 0.05em;
	margin: 3em 0 2em;
	padding: 1em;
	width: 20em;
	border: 3px #ddede1 solid;
	border-radius: 20px;
}

.new_trip_form_button:hover {
	font-style: italic;
}
#dashboard {
	display: flex;
	flex-direction: column;
	width: 100vw;
	text-align: center;
}

#dashboard header {
	color: white;
	display: flex;
	font-size: 1em;
	justify-content: space-around;
	align-items: center;
	background-color: #56638A;
	height: 3em;
	margin-bottom: 1em;
}

#dashboard header h1 {
	color: #fbe391;
	font-size: 1.5em;
}

#dashboard header button {
	color: #FFEFB9;
	font-size: 1em;
	background: none;
	border: none;
	padding: 0.5em;
}

#dashboard header button:hover {
	color: #8BAEBD;
	font-style: italic;
}

#dashboard main {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
body {
  background-color: #fff;
  font-family: 'Roboto', sans-serif;
  font-size: 1.1em;
}
